no-webp .imagen-contacto{
    background-image:url('../img/tienda/banner.jpg') ;
}
.webp .imagen-contacto{
    background-image:url('../img/tienda/banner.webp') ;

    }

.imagen-contacto{
    background-position: center center;
    background-size: cover;
    padding: $separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2{
        font-size: 4rem;
        font-weight: $black;
        color: $blanco;
    }

    p{
        font-size: 1.8rem;
        color: $blanco;
    }
}