  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 100%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #ec0000;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #d30000;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

/** UI **/
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
  }
}
.header h1 {
  color: #000000;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}

.contenido-header {
  max-width: 150rem;
}
@media (min-width: 768px) {
  .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .contenido-header .navegacion {
    display: flex;
    gap: 2rem;
  }
}
.contenido-header .navegacion a {
  display: block;
  text-align: center;
  color: #000000;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .contenido-header .navegacion a {
    margin: 0;
  }
}
.contenido-header .navegacion a:hover {
  color: #71B100;
}
.contenido-header .imagen-header img {
  max-width: 50%;
  max-height: 20px;
}

.footer {
  padding: 2.5rem;
  background-color: #FFFFFF;
  margin-bottom: 0;
  text-align: center;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.nav-footer {
  visibility: visible;
  opacity: 20;
  height: auto;
}
.nav-footer a {
  color: #FFFFFF;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #000000;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/** icono del menu responsibe **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono img {
  height: 10rem;
  text-transform: center;
}
.icono h3 {
  text-transform: uppercase;
}

.galeria-principal img {
  width: 720px;
  height: 480x;
  display: block;
  margin: auto;
}

.prueba2 {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
}

.video {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  margin-right: auto;
}
.video .overlay {
  position: absolute;
  height: 100%;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 1024px) {
  .video video {
    width: 60%;
  }
}

.contenido-video {
  z-index: 10;
}
.contenido-video h2 {
  color: #71B100;
  font-size: 7rem;
  margin: 0;
}
.contenido-video p {
  background-color: #71B100;
  display: inline-block;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #e1e1e1;
  border: 1px solid #e1e1e1;
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
  margin-bottom: 2rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
}
.anuncio img {
  max-width: 100%;
  max-height: 40rem;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

no-webp .imagen-contacto {
  background-image: url("../img/tienda/banner.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/tienda/banner.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-blog a {
  color: #000000;
}
@media (min-width: 768px) {
  .texto-blog a {
    margin-top: 0;
  }
}
.texto-blog h1,
.texto-blog h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-blog h1::after,
.texto-blog h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.texto-blog span {
  color: #E08709;
}

@media (min-width: 768px) {
  .contenido-ropa {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }

  .precio-tallas {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario .forma-contacto {
  max-width: 30%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario textarea {
  height: 20rem;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

/** Paginas internas **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding-left: 1rem 0 3rem 0;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.exito {
  background-color: #71B100;
}
.alerta.error {
  background-color: #ec0000;
}

.bt-crear {
  display: flex;
  justify-content: center;
  margin: 0;
}
.bt-crear a {
  margin-left: 1rem;
}

table.listado-ropa {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.listado-ropa thead {
  background-color: #71B100;
}
table.listado-ropa thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.listado-ropa .imagen-tabla {
  width: 10rem;
}

.imagen-actualizar {
  width: 20rem;
}